<template>
  <div class="login_container">
    <div class="login_box">
      <!-- 头像区 -->
      <div class="avatar_box">
        <img src="../assets/admin.png" alt />
      </div>
      <!-- 登录表单区域 -->
      <el-form
        ref="loginFormRef"
        label-width="0px"
        :model="loginForm"
        class="login_form"
        :rules="loginRules"
      >
        <!-- 用户名 -->
        <el-form-item label prop="username">
          <el-input prefix-icon="iconfont icon-zhanghao" v-model="loginForm.username"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item label prop="password">
          <el-input type="password" prefix-icon="iconfont icon-mima" v-model="loginForm.password"></el-input>
        </el-form-item>
        <!--按钮 -->
        <el-form-item class="btn">
          <el-button type="primary" @click="login">登录</el-button>
          <el-button type="info" @click="resetLoginForm">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      loginRules: {
        username: [
          { required: true, message: '请输入账户名称', trigger: 'blur' },
          { min: 1, max: 11, message: '长度在 1到 11个字符', trigger: 'blur' }
        ],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 重置表单
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields()
    },
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        //预验证
        console.log(valid)
        if (!valid) return
        const { data: res } = await this.$http.post(
          '/user/login',
          this.loginForm
        )
        if (res.errorCode == 20002)
          return this.$Message.error('账号或密码错误，登录失败！')
        if (res.data.role !== 2)
          return this.$Message.error('您的账号暂无权限访问！')
        this.$Message.success('登录成功！')
        console.log(res.data.token)
        window.sessionStorage.setItem('token', res.data.token)
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
  // background-color: #16c2c2;
   background-image: url('../assets/874.jpg');
   background-repeat: no-repeat;
   background-position-x: center;
   background-size: 100%;
  height: 100%;
}
.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  .avatar_box {
    height: 100px;
    width: 100px;
    padding: 10px;
    border-radius: 50%;
    background-color: #ffffff;
    box-shadow: 0 0 10px #dddddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
}
.btn {
  display: flex;
  justify-content: flex-end;
}
.login_form {
  position: absolute;
  bottom: 0;
  padding: 0 20px;
  width: 100%;
  box-sizing: border-box;
}
.el-button--primary {
  background-color: #16c2c2;
  border-color: #16c2c2;
}
</style>