<template>
  <div>
    <!-- 面包蟹导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>话题管理</el-breadcrumb-item>
      <el-breadcrumb-item>话题分类</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible = true">添加话题</el-button>
        </el-col>
      </el-row>
      <!-- 表格区域 -->
      <el-table :data="topicList" border stripe class="mt-10">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="话题名称" prop="title"></el-table-column>
        <el-table-column label="话题描述" prop="desc"></el-table-column>
        <el-table-column label="所属分类" prop="topicClass"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <!-- 修改按钮 -->
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <!-- 删除按钮 -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removePostClass(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <!-- 分页区域 -->
      <el-pagination
        hide-on-single-page
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[2,5,8,10]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!-- 添加分类 -->
      <!-- 添加用户 -->
      <el-dialog
        title="添加话题"
        :visible.sync="addDialogVisible"
        width="50%"
        @close="addDialogClose"
      >
        <!-- 内容主题区域 -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
          <el-form-item label="话题名称" prop="classname">
            <el-input v-model="addForm.classname"></el-input>
          </el-form-item>
          <el-form-item label="所属分类" prop="classname">
            <el-input v-model="addForm.classname"></el-input>
          </el-form-item>
        </el-form>
        <!-- 底部区域 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false">取 消</el-button>
          <el-button type="primary" >确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      addForm: {
        classname: ''
      },
      addFormRules: {
        classname: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      addDialogVisible: false,
      topicList: [],
      page: 1,
      pageSize: 5,
      total: 10
    }
  },
  created() {
    this.getPostClassList()
    // this.gettopicClass()
  },
  //获取文章分类
  methods: {
    handleSizeChange(newsize) {
      //监听pagesize 改变事件
      console.log(newsize)
      this.pageSize = newsize
      // this.getUserList()
      this.getPostClassList()
    },
    //监听页码值改变的事件
    handleCurrentChange(newpage) {
      this.page = newpage
      console.log(newpage)
      // this.getUserList()
      this.getPostClassList()
    },
    async getPostClassList() {
      // const { data: res } = await this.$http.get('/topicclass')
      const { data: res } = await this.$http.get(
        '/getTopic/' + this.page + '/' + this.pageSize
      )
      let list =  await this.gettopicClass()
      res.data.data.list.forEach(item=>{
        list.forEach(i=>{
          if(item.topic_class_id == i.id){
            item.topicClass = i.classname
          }
        })
      })
      // console.log(res.data.list)
      this.total = res.data.data.total
      this.topicList = res.data.data.list
    },
    // addDialogClose() {
    //   this.$refs.addFormRef.resetFields()
    // },
      //清空搜索框
    clearInput() {
      this.getUserList()
    },
    //添加用户对话框的关闭事件
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },
    async gettopicClass (){
      const { data: res } = await this.$http.get('/topicclass')
      // console.log(res.data.list)
      
      return res.data.list
    },
    // async addPostClass() {
    //   // console.log(this.classname)
    //   const { data: res } = await this.$http.post('/addPostClass', {
    //     classname: this.addForm.classname
    //   })
    //   if (res.msg !== '添加成功') {
    //     this.$Message.Error('添加分类失败')
    //   }
    //   this.$Message.success('添加分类成功')
    //   this.addDialogVisible = false
    //   this.getPostClassList()
    // },
    async removePostClass(id) {
      // 弹框
      const confirmResult = await this.$confirm(
        '此操作将永久删除该分类, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$Message.info('已取消删除')
      }
      // console.log(id)
      const { data: res } = await this.$http.post('/deletPostClass', { id: id })
      if (res.msg !== '删除成功') {
        this.$Message.Error('删除分类失败')
      }
      this.$Message.success('删除分类成功')
      this.addDialogVisible = false
      this.getPostClassList()
    }
  }
}
</script>
<style lang="less" scoped>
.el-button--primary {
  color: #fff;
  background-color: rgb(22, 194, 194) !important;
  border-color: rgb(22, 194, 194) !important;
}
</style>