<template>
  <div>
    <!-- 面包蟹导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>数据统计</el-breadcrumb-item>
      <el-breadcrumb-item>统计</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 统计数字区域 -->
      <el-row :gutter="20">
        <el-col :span="4">
          <el-card :body-style="{ padding: '0px' }">
            <div class="pd-1">
              <span>文章总量</span>
              <div class="pd-1">
                <div class="pd-1 text-center font-sm font-weight-bold">{{total.postcount}}</div>
                <div class="text-right pr-10 text-light-muted font-smaller">今日+{{total.newpost}}</div>
              </div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="4">
          <el-card :body-style="{ padding: '0px' }">
            <div class="pd-1">
              <span>评论总量</span>
              <div class="pd-1">
                <div class="pd-1 text-center font-sm font-weight-bold">{{total.commentcount}}</div>
                <div class="text-right pr-10 text-light-muted font-smaller">今日+{{total.newcomment}}</div>
              </div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="4">
          <el-card :body-style="{ padding: '0px' }">
            <div class="pd-1">
              <span>用户总量</span>
              <div class="pd-1">
                <div class="pd-1 text-center font-sm font-weight-bold">{{total.userCount}}</div>
                <div class="text-right pr-10 text-light-muted font-smaller">今日+{{total.newuser}}</div>
              </div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="4">
          <el-card :body-style="{ padding: '0px' }">
            <div class="pd-1">
              <span>转发总量</span>
              <div class="pd-1">
                <div class="pd-1 text-center font-sm font-weight-bold">50</div>
                <div class="text-right pr-10 text-light-muted font-smaller">今日+2</div>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
      <el-row>
        <el-col :span="12" class="mt-20">
          <el-button type="info" autofocus plain @click="getTime('d')">今日</el-button>
          <el-button type="info" plain @click="getTime('yesterday')">昨天</el-button>
          <el-button type="info" plain @click="getTime('w')">本周</el-button>
          <el-button type="info" plain @click="getTime('lastweek')">上周</el-button>
          <el-button type="info" plain @click="getTime('m')">本月</el-button>
          <el-button type="info" plain @click="getTime('lastmonth')">上月</el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="8">
          <div class="mt-20" id="main" style="width:600px;height:400px;"></div>
        </el-col>
        <el-col :span="8">
          <div class="mt-20 pl-20" id="main2" style="width:600px;height:400px;"></div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
import echarts from 'echarts'
import moment from '../../assets/js/moment.min.js'
export default {
  data() {
    return {
      time: 'd',
      xAxis: [],
      y: [],
      total: {},
      userCount: {},
      postCount: {},
      commentCount: {},
      pie: [
        { value: 0, name: '西安市' },
        { value: 0, name: '安康市' },
        { value: 0, name: '渭南市' },
        { value: 0, name: '汉中市' },
        { value: 0, name: '咸阳市' },
        { value: 0, name: '榆林市' },
        { value: 0, name: '延安市' },
        { value: 0, name: '宝鸡市' },
        { value: 0, name: '商洛市' },
        { value: 0, name: '其他' }
      ]
    }
  },
  created() {
    // console.log(this.getAllDate('2019-3-1', '2019-3-3'))
    // this.getData()
    this.getTotal()
    this.getData('today')
    this.getPieData()
    // this.xAxis.push(now, yesterday)
  },
  mounted() {},
  methods: {
    myChart() {
      // 基于准备好的dom，初始化echarts实例
      var myChart = echarts.init(document.getElementById('main'))
      var option = {
        title: {
          text: '统计图表'
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['总量']
        },
        xAxis: {
          data: ['用户量', '文章量', '评论量']
        },
        yAxis: {},
        series: [
          {
            name: '总量',
            type: 'bar',
            showBackground: true,
            data: this.y,
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  position: 'top',
                  textStyle: {
                    color: 'black',
                    fontSize: 16
                  }
                },
                color: function(params) {
                  let colorList = ['#c23531', '#2f4554', '#61a0a8']
                  return colorList[params.dataIndex]
                }
              }
            }
          }
        ]
      }
      myChart.setOption(option)
    },
    myPie() {
      var myChart = echarts.init(document.getElementById('main2'))
      var option = {
        title: {
          text: '校园交友APP用户来源',
          subtext: '仅中国',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c}人 ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 'right',
          data: [
            '西安市',
            '安康市',
            '渭南市',
            '汉中市',
            '咸阳市',
            '榆林市',
            '延安市',
            '宝鸡市',
            '商洛市',
            '其他'
          ]
        },
        series: [
          {
            name: '访问来源',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: this.pie,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
      myChart.setOption(option)
    },
    async getData(time) {
      let params = {
        data: time
      }
      // console.log(params.data)
      const { data: res } = await this.$http.post('/getCount', params)
      this.y = []
      this.y.push(
        res.data.data.userCount,
        res.data.data.postCount,
        res.data.data.commentCount
      )
      // console.log(this.y)
      this.myChart()
    },
    getTime(time) {
      this.time = time
      this.getData(time)
    },
    async getPieData() {
      const { data: res } = await this.$http.get('/getUserinfo')
      console.log(res)
      res.data.data.path.forEach(item => {
        if (item === '西安市') {
          this.pie[0].value++
        }
        if (item === '安康市') {
          this.pie[1].value++
        }
        if (item === '渭南市') {
          this.pie[2].value++
        }
        if (item === '汉中市') {
          this.pie[3].value++
        }
        if (item === '咸阳市') {
          this.pie[4].value++
        }
        if (item === '榆林市') {
          this.pie[5].value++
        }
        if (item === '延安市') {
          this.pie[6].value++
        }
        if (item === '宝鸡市') {
          this.pie[7].value++
        }
        if (item === '商洛市') {
          this.pie[8].value++
        }
        if (item === '其他') {
          this.pie[9].value++
        }
      })
      this.myPie()
      console.log(this.pie)
    },
    //获取昨天数据
    async getTotal() {
      const { data: res } = await this.$http.get('/getCount')
      // console.log(res)
      this.total = res.data.data
    }
  }
}
</script>
 <style lang="less" scoped>
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: '';
}

.clearfix:after {
  clear: both;
}
.count {
  display: flex;
  align-items: center;
  padding: 10px;
}
</style>
