<template>
  <el-container class="home-container">
    <!-- 头部 -->
    <el-header class>
      <div>
        <img src="../assets/bgc.jpg" alt style="width:50px;height:50px" />
        <span>安康学院校园交友APP管理系统</span>
      </div>
      <el-button @click="logout" type="info">退出</el-button>
    </el-header>
    <!-- 主题区域 -->
    <el-container>
      <!-- 侧边栏 -->
      <el-aside width="200px">
        <!-- 侧边栏菜单区 -->
        <el-menu
          background-color="#313434"
          text-color="#fff"
          router
          :default-active="activepath"
          active-text-color="#1a9191"
          unique-opened
        >
          <!-- 一级菜单 -->
          <el-submenu :index="index+''" v-for="(item,index) in list" :key="index">
            <!-- 一级菜单模板 -->
            <template slot="title">
              <!-- 图标 -->
              <i class="iconfont pr-10" :class="item.icon"></i>
              <span>{{item.name}}</span>
            </template>
            <!-- 二级菜单 -->
            <el-menu-item
              @click="saveNavState(item2.path)"
              :index="item2.path"
              v-for="item2 in item.children"
              :key="item2.id"
            >
              <template slot="title">
                <!-- 图标 -->
                <i class="el-icon-menu"></i>
                <span>{{item2.name}}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!-- 右侧主体 -->
      <el-main>
        <!-- 路由占位符 -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
export default {
  created() {
    this.activepath = window.sessionStorage.getItem('activepath')
  },
  data() {
    return {
      list: [
        {
          name: '数据统计',
          icon: 'icon-tongji',
          children: [{ name: '统计', id: 1, path: '/count' }]
        },
        {
          name: '用户管理',
          icon: 'icon-yonghu',
          children: [{ name: '用户列表', id: 2, path: '/users' }]
        },
        {
          name: '帖子管理',
          icon: 'icon-wenzhang',
          children: [
            { name: '帖子分类', id: 3, path: '/posts' },
            { name: '内容管理', id: 4, path: '/cotent' },
          ]
        },
        {
          name: '话题管理',
          icon: 'icon-huati',
          children: [{ name: '话题分类', id: 6, path: '/topic' }]
        },
        {
          name: '敏感词管理',
          icon: 'icon-quanxian',
          children: [
            { name: '敏感词设置', id: 7, path: '/sensitive' }
            // { name: '权限列表', id: 8, path: '/rights' }
          ]
        }
      ],
      activepath: '/count' //被激活的链接
    }
  },
  methods: {
    logout() {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    // 保存激活状态
    saveNavState(activepath) {
      window.sessionStorage.setItem('activepath', activepath)
      this.activepath = activepath
    }
  }
}
</script>
<style lang="less" scoped>
.home-container {
  height: 100%;
}
.el-header {
  background-color: #1da3a3;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #ffffff;
  font-size: 16px;
  > div {
    display: flex;
    align-items: center;
    > img {
      border-radius: 5px;
      // background-color: #16c2c2;
      padding: 0 10px;
    }
  }
}
.el-aside {
  background-color: #313434;
}
.el-main {
  // background-color: #eaedf1;
}
.el-button--info {
  background-color: #1a9191;
  border-color: #1a9191;
}
.el-menu {
  border-right: none;
}
</style>