<template>
  <div>
    <!-- 面包蟹导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!-- 搜索添加区域 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="请输入内容" v-model="keywords" clearable @clear="clearInput">
            <el-button slot="append" icon="el-icon-search" class="btn" @click="searchUser"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button class="bgc text-white" @click="addDialogVisible = true">添加用户</el-button>
        </el-col>
      </el-row>
      <!-- 用户列表区域 -->
      <el-table :data="userList" border stripe class="mt-10">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="昵称" prop="username"></el-table-column>
        <el-table-column label="邮箱" prop="email"></el-table-column>
        <el-table-column label="电话" prop="phone"></el-table-column>
        <el-table-column label="角色" prop="role"></el-table-column>
        <el-table-column label="状态" prop="status">
          <template slot-scope="scope">
            <el-switch
              @change="changeStatus(scope.row)"
              v-model="scope.row.status"
              active-color="#16C2C2"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <!-- 修改按钮 -->
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <!-- 删除按钮 -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeuser(scope.row.id)"
            ></el-button>
            <!-- 分配角色 -->

            <el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="dialogVisible = true,user.user_id = scope.row.id,user.role = scope.row.role 
                options.forEach(item=>item.label==scope.row.role?item.disabled = true:item.disabled=false)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
        hide-on-single-page
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[2,5,8,10]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!-- 添加用户 -->
    <el-dialog title="添加用户" :visible.sync="addDialogVisible" width="50%" @close="addDialogClose">
      <!-- 内容主题区域 -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="phone">
          <el-input v-model="addForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="角色" prop="role">
          <el-input v-model="addForm.role"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改用户角色 -->

    <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
          :disabled="item.disabled"
        ></el-option>
      </el-select>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="changeRole">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getUserList()
  },
  data() {
    // 校验手机号的验证规则
    var checkPhone = (rules, value, cb) => {
      const regPhone = /^1[34578]\d{9}$/
      if (regPhone.test(value)) {
        return cb() //合法的邮箱
      }
      cb(new Error('请输入合法的手机号'))
    }
    return {
      addForm: {
        username: '',
        password: '',
        phone: '',
        role: ''
      }, //添加用户
      addFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          {
            min: 1,
            max: 11,
            message: '用户名的长度在 1到 11个字符',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '用户密码的长度在 6到 15个字符',
            trigger: 'blur'
          }
        ],
        phone: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          {
            min: 10,
            max: 11,
            message: '用户手机号的长度是11个字符',
            trigger: 'blur'
          },
          { validator: checkPhone, trigger: 'blur' }
        ],
        role: [{ required: true, message: '请指定用户角色', trigger: 'blur' }]
      },
      value: '',
      label: '',
      user: {
        user_id: '',
        role: ''
      },
      options: [
        { value: '选项1', label: '版主', disabled: false },
        { value: '选项2', label: '管理员', disabled: false },
        { value: '选项3', label: '用户', disabled: false }
      ],
      dialogVisible: false, //控制修改用户角色
      addDialogVisible: false, //控制添加用户的显示与隐藏
      userList: [],
      page: 1,
      pageSize: 5,
      total: 10,
      keywords: ''
    }
  },
  methods: {
    async changeRole() {
      // JSON.stringify(this.user)
      let user = JSON.stringify(this.user)
      let u = JSON.parse(user)
      console.log(this.value)
      if (this.value == '选项1') {
        u.role = 1
      } else if (this.value == '选项2') {
        u.role = 2
      } else {
        u.role = 0
      }
      // let user = JSON.stringify(this.user)

      const { data: res } = await this.$http.post('/changeRole', u)
      // console.log(JSON.stringify(this.user))
      // console.log(res)
      if (res.msg !== '获取成功') {
        this.$Message.Error('修改失败')
      }
      this.$Message.success('修改成功')
      this.dialogVisible = false
      this.getUserList()
      this.value = ''
    },
    handleSizeChange(newsize) {
      //监听pagesize 改变事件
      console.log(newsize)
      this.pageSize = newsize
      this.getUserList()
    },
    //监听页码值改变的事件
    handleCurrentChange(newpage) {
      this.page = newpage
      console.log(newpage)
      this.getUserList()
    },
    //修改用户状态
    changeStatus(userinfo) {
      let status = userinfo.status == true ? 1 : 0
      let user_id = userinfo.id
      this.changuserStatus({ user_id: user_id, status: status })
    },
    async getUserList() {
      const { data: res } = await this.$http.get(
        '/uerslist/' + this.page + '/' + this.pageSize
      )
      console.log(res)
      // let list = []
      res.data.data.list.forEach(item => {
        item.status === 1 ? (item.status = true) : (item.status = false)
        if (item.role == 2) {
          item.role = '管理员'
          // item.status = true
        } else if (item.role == 1) {
          item.role = '版主'
        } else {
          item.role = '用户'
        }
      })
      this.userList = res.data.data.list
      this.total = res.data.data.total
    },
    //修改用户状态
    async changuserStatus(parmas) {
      const { data: res } = await this.$http.post('/editUserStatus', parmas)
      console.log(res)
    },
    //查询用户
    async searchUser() {
      let parmas = {
        keyword: this.keywords,
        page: this.page
      }
      const { data: res } = await this.$http.post('/search/user', parmas)
      console.log(this.keywords)
      this.userList = res.data.list
      this.total = res.data.list.length
    },
    //清空搜索框
    clearInput() {
      this.getUserList()
    },
    //添加用户对话框的关闭事件
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },

    //点击按钮添加新用户
    addUser() {
      this.$refs.addFormRef.validate(async vailde => {
        //表单预验证
        // console.log(vailde)
        if (!vailde) return
        //添加添加用户的请求
        const { data: res } = await this.$http.post('adduser', this.addForm)
        console.log(res)
        if (res.msg !== '添加成功') {
          this.$Message.Error('添加用户失败')
        }
        this.$Message.success('添加用户成功')
        this.addDialogVisible = false
        this.getUserList()
      })
    },
    //根据id删除对应的用户
    async removeuser(id) {
      // 弹框
      const confirmResult = await this.$confirm(
        '此操作将永久删除该用户, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      //如果用户确认删除，则返回值为字符串
      console.log(confirmResult)
      if (confirmResult != 'confirm') {
        return this.$Message.info('已取消删除')
      }
      console.log('确认了删除')
      const { data: res } = await this.$http.post('/deleteuser', {
        user_id: id
      })
      if (res.msg !== '删除成功') {
        this.$Message.Error('删除用户失败')
      }
      this.$Message.success('删除用户成功')
      this.getUserList()
    }
  }
}
</script>
<style lang="less" scoped>
.el-button--primary {
  color: #fff;
  background-color: rgb(22, 194, 194) !important;
  border-color: rgb(22, 194, 194) !important;
}
.el-button:active {
  color: #ffffff !important;
  border-color: #1da3a3 !important;
  outline: 0;
}
.el-button:focus,
.el-button:hover {
  color: #c6e2ff !important;
}
</style>