<template>
  <div>
    <!-- 面包蟹导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>帖子管理</el-breadcrumb-item>
      <el-breadcrumb-item>帖子分类</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible = true">添加分类</el-button>
        </el-col>
      </el-row>
      <!-- 表格区域 -->
      <el-table :data="postclassList" border stripe class="mt-10">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="分类名称" prop="classname"></el-table-column>
        <el-table-column label="是否有效" prop="valid"></el-table-column>
        <el-table-column label="排序" prop="Sort"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <!-- 修改按钮 -->
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <!-- 删除按钮 -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removePostClass(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <!-- 添加分类 -->
      <!-- 添加用户 -->
      <el-dialog
        title="添加帖子分类"
        :visible.sync="addDialogVisible"
        width="50%"
        @close="addDialogClose"
      >
        <!-- 内容主题区域 -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
          <el-form-item label="分类名称" prop="classname">
            <el-input v-model="addForm.classname"></el-input>
          </el-form-item>
        </el-form>
        <!-- 底部区域 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addPostClass">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      addForm: {
        classname: ''
      },
      addFormRules: {
        classname: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      addDialogVisible: false,
      postclassList: []
    }
  },
  created() {
    this.getPostClassList()
  },
  //获取文章分类
  methods: {
    async getPostClassList() {
      const { data: res } = await this.$http.get('/postclass')
      console.log(res.data.list)
      if (res.data.list.length === 0) {
        return this.$Message.error('获取数据失败')
      }
      res.data.list.forEach(item => {
        item.valid = '是'
        item.Sort = '一级'
      })
      this.postclassList = res.data.list
    },
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },
    async addPostClass() {
      // console.log(this.classname)
      const { data: res } = await this.$http.post('/addPostClass', {
        classname: this.addForm.classname
      })
      if (res.msg !== '添加成功') {
        this.$Message.Error('添加分类失败')
      }
      this.$Message.success('添加分类成功')
      this.addDialogVisible = false
      this.getPostClassList()
    },
    async removePostClass(id) {
      // 弹框
      const confirmResult = await this.$confirm(
        '此操作将永久删除该分类, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$Message.info('已取消删除')
      }
      // console.log(id)
      const { data: res } = await this.$http.post('/deletPostClass', { id: id })
      if (res.msg !== '删除成功') {
        this.$Message.Error('删除分类失败')
      }
      this.$Message.success('删除分类成功')
      this.addDialogVisible = false
      this.getPostClassList()
    }
  }
}
</script>
<style lang="less" scoped>
.el-button--primary {
  color: #fff;
  background-color: rgb(22, 194, 194) !important;
  border-color: rgb(22, 194, 194) !important;
}
</style>