<template>
  <div>
    <!-- 面包蟹导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>敏感词管理</el-breadcrumb-item>
      <el-breadcrumb-item>敏感词设置</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!-- 搜索添加区域 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="搜索敏感词" v-model="keywords" clearable>
            <el-button slot="append" icon="el-icon-search" class="btn" ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button class="bgc text-white" @click="addDialogVisible = true">添加敏感词</el-button>
        </el-col>
      </el-row>
      <!-- 敏感词列表 -->
      <el-table :data="sensitive" border stripe class="mt-10">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="敏感词" prop="name"></el-table-column>
        <el-table-column label="创建时间">
           <template slot-scope="scope">{{scope.row.create_time|formateDate}}</template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <!-- 修改按钮 -->
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <!-- 删除按钮 -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeuser(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
        hide-on-single-page
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[2,5,8,10]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>
<script>
import moment from '@/assets/js/moment.min.js'
export default {
  data() {
    return {
      sensitive: [],
      keywords:'',
      page: 1,
      pageSize: 5,
      total: 10
    }
  },
    filters: {
    formateDate(value) {
        return moment(value*1000).format('YYYY-MM-DD HH:mm:ss');
    }
  },
  created() {
    this.getsensitive()
  },
  methods: {
    async getsensitive() {
      const { data: res } = await this.$http.get(
        'getSensitive' + '/' + this.page + '/' + this.pageSize
      )
      console.log(res.data.data)
      this.sensitive = res.data.data.list
      this.total = res.data.data.total
        console.log( this.sensitive)
    },
    handleSizeChange(newsize) {
      //监听pagesize 改变事件
      console.log(newsize)
      this.pageSize = newsize
      // this.getUserList()
      this.getsensitive()
    },
    //监听页码值改变的事件
    handleCurrentChange(newpage) {
      this.page = newpage
      console.log(newpage)
      // this.getUserList()
      this.getsensitive()
    },

  }
}
</script>
<style lang="less" scoped>
.el-button--primary {
  color: #fff;
  background-color: rgb(22, 194, 194) !important;
  border-color: rgb(22, 194, 194) !important;
}
</style>